
.image {
    position: relative;
   
    
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-color: rgb(183, 91, 91);
    scroll-behavior: smooth;
}
.img{
    height: 90vh;
    width:90vw;
    scroll-behavior: auto smooth;
    display: flex;
    justify-content: center;
    

}
.sat{
    height:100%;
    width: 100%;
   
}

.next-btn{
border: none;
right: 15px;
position: absolute;
top:50%;
display:flex;
justify-content: center;
align-items: center;
background:linear-gradient(90deg,rgba(248, 201, 201, 0) 0%,#f2a3a6 100%);
cursor: pointer;
z-index: 9;
}
.pre-btn{
    border: none;
left: 15px;
position: absolute;
top:50%;
display:flex;
justify-content: center;
align-items: center;
background:linear-gradient(90deg,rgba(238, 245, 147, 0) 0%,#f2a3a6 100%);
cursor: pointer;
z-index: 9;
}
.p{
    font-size: 1.5rem;
    color: rgb(249, 244, 244);
}



 

