.nav {
    height: 60px;
    width: 100%;
    display: flex;
    align-items: center;
    position: relative;
    padding: 0.5rem 0rem;
    background-color: rgb(183, 91, 91);
    color: black;
    box-shadow: 0 2px 2px 2px rgba(9, 9, 9, 0.23);
  }
  .brand{
    text-decoration: none;
    color: white;
    font-size: 2rem;
    margin-left: 1rem;
    font-weight: bold;
    font-family: 'Ubuntu', sans-serif;
   ;
  }
  .navigation_menu {
    margin-left: auto;
  }
  .navigation_menu ul {
    display: flex;
    padding: 0;
  }
  .navigation_menu li {
   
    list-style-type: none;
    margin: 0 1rem;
}
.nav_link{
    
    text-decoration: none;
    display: block;
    width: 100%;
    color: white;
    font-size: 1.5rem;
  }