.gallery{
    width:100%;
            display:flex;
            flex-flow: row wrap;
            /* background-color: rgb(245, 229, 209); */
            padding-left: 60px;
            padding-top: 30px;
            padding-bottom: 30px;
            
}
.pics{
    
    display: flex;
    justify-content: center;
   
    flex-basis:20%;
            width:50%;
            padding:10px;
            
            margin:8px;
            box-shadow: 1px 1px 1px 1px rgb(86, 88, 86);
            padding: 10px;
}

.h1{
    margin-left: 650px;
    font-style: italic;
    color: rgb(246, 95, 95);
    font-size: 3rem;
}
.div{
    width: 100vw;
    background-color:   rgb(245, 229, 209);
    border-top-style: dotted;
    border-color: rgb(245, 229, 209);
    border-width: 5px;

    
}

@media only screen and (max-width: 300px) { 
    .box {
        flex-basis: 100%;
    }
}
  
@media only screen and (max-width:500px) {
    .box {
        flex-basis: 40%;
    }
}